.section {

  &__bakery {
    padding-bottom: 80px;
  }

  &__bakershops {
    height: 1112px;
    background-color: $color-light-brown;

    @media (max-width: 1200px) {
      height: 1000px;
    }

    @media (max-width: 1000px) {
      height: 900px;
    }

    @media (max-width: 800px) {
      height: 800px;
    }

    @media (max-width: 600px) {
      height: 627px;
    }
  }

  &__about {
    display: flex;
    justify-content: center;
  }
}
