.bakery {
  
  &__content {
    @include flex-between;
    margin-top: 27px;
    margin-bottom: 135px;

    @media (max-width: 1300px) {
      @include flex-column;
    }

    @media (max-width: 900px) {
      margin-top: 5px;
      margin-bottom: 40px;
    }

    &--reverse {
      @media (max-width: 1300px) {
        flex-direction: column-reverse;
      }
    }
  }

  &__title {
    margin-top: 40px;
  }

  &__button {
    display: block;
    margin: auto;
  }
}
